.panel-footer {
    background-image: -webkit-linear-gradient(top,#ffffff 0,#7e7e84 100%);
    color: #111;
    height: 100px;
    /* background: #286090; */
    border: #fff 1px solid;
    -webkit-box-shadow: 1px 1px 1px #fff;
